import "isomorphic-fetch"

const About = ({starts}) =>
<div>
  Next starts: {starts}
</div>

About.getInitialProps = async ({req}) =>{
  const res = await fetch('https://api.github.com/repos/zeit/next.js')
  const json = await res.json()
  return {starts: json.stargazers_count}
}

export default About